import axios from 'axios'
import {Notification, Message} from 'element-ui';
import {paramsFilter} from '@/utils'
import {$SERVICE_ERR} from '@/config/dictionary'

/**
 *
 * $HTTP_MAP 
 */
let $HTTP_MAP = {};
export default function fetch(options) {
    // xhr
    return new Promise((resolve, reject) => {

        const instance = axios.create({
            baseURL: 'https://www.easy-mock.com/mock/5c7bee92852a035c4301174e/api'
            // baseURL: 'http://' + window.location.hostname + ':9090/api/'
        });

        // http request 
        instance.interceptors.request.use(
            config => {
                config.data = paramsFilter(config.data);
                return config;
            },
            err => {
                return Promise.reject(err);
            });

        // http response  
        instance.interceptors.response.use(function (response) {
            // 
            return response;
        }, function (err) {
            if (!!err.response && !!err.response.data.message) {
                return Promise.reject(Error(err.response.data.message));
            } else {
                return Promise.reject(err);
            }
        });


        /**
         * ,
         */
        if (options.url in $HTTP_MAP) {
            Notification({
                title: '',
                message: '',
                type: 'warning',
                duration: 800,
                showClose: false
            });
        } else {
            $HTTP_MAP[options.url] = true;
            //
            instance(options)
                .then((res) => {
                    resolve(res);
                })
                .catch((error) => {
                    reject(error);
                })
                .finally(() => {
                    delete $HTTP_MAP[options.url];
                })
        }
    })
}
